.App {
  text-align: center;
  font: 10px/1.5 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  padding: 0px 10px 20px;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


h2{
  font-size: 20px;
  padding:20px 0 10px;
  color:#454545;
  margin: 0;
}

table{
  border-collapse: collapse;
  color: #999;
  width: 100%;
}

table td,
table th{
  padding: 5px 15px;
  text-align: right;
  border: 1px solid #dfe0de;
}

table thead{
  background-color: #f1f1f1;
}

td b{
  color: #666;
}

tr:last-child td{
  background-color: #f1f1f1;
}

@media screen and (max-width: 660px) {

  .exchange-list td:first-child {
    display: none;
  }
  
  .exchange-list  th:first-child {
    display: none;
  }
  
  .exchange-list  td:last-child {
    display: none;
  }
  
  .exchange-list  th:last-child {
    display: none;
  }


  .exchangedAssets th:last-child,
  .exchangedAssets th:nth-child(4),
  .exchangedAssets td:last-child,
  .exchangedAssets td:nth-child(4){
    display: none;
  }
}


.exchangedAssets tr:last-child td{
  background-color: transparent;
}


.loadEffect{
  width: 100px;
  height: 100px;
  display: inline-block;
  position: relative;
  margin: 0 auto;
  zoom: .1;
}
.loadEffect span{
  display: inline-block;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #666;
  position: absolute;
  -webkit-animation: load 1.04s ease infinite;
}
@-webkit-keyframes load{
  0%{
      opacity: 1;
  }
  100%{
      opacity: 0.2;
  }
}
.loadEffect span:nth-child(1){
  left: 0;
  top: 50%;
  margin-top:-8px;
  -webkit-animation-delay:0.13s;
}
.loadEffect span:nth-child(2){
  left: 14px;
  top: 14px;
  -webkit-animation-delay:0.26s;
}
.loadEffect span:nth-child(3){
  left: 50%;
  top: 0;
  margin-left: -8px;
  -webkit-animation-delay:0.39s;
}
.loadEffect span:nth-child(4){
  top: 14px;
  right:14px;
  -webkit-animation-delay:0.52s;
}
.loadEffect span:nth-child(5){
  right: 0;
  top: 50%;
  margin-top:-8px;
  -webkit-animation-delay:0.65s;
}
.loadEffect span:nth-child(6){
  right: 14px;
  bottom:14px;
  -webkit-animation-delay:0.78s;
}
.loadEffect span:nth-child(7){
  bottom: 0;
  left: 50%;
  margin-left: -8px;
  -webkit-animation-delay:0.91s;
}
.loadEffect span:nth-child(8){
  bottom: 14px;
  left: 14px;
  -webkit-animation-delay:1.04s;
}

